import React from "react";
import "./Morafat.css";
const Textmorafat = () => {
  return (
    <div>
      <div
        class="accordion accordion-flush  bg-dark bodnone "
        id="accordionExample"
      >
        <div class="accordion-item text-white bodnone  bg-dark">
          <h2 class="accordion-header bodnone " id="headingOne">
            <button
              class="accordion-button bodnone bg-dark border-0"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseOne"
              aria-expanded="true"
              aria-controls="collapseOne"
            >
              القضايا الجنائية
            </button>
          </h2>
          <div
            id="collapseOne"
            class="accordion-collapse collapse show"
            aria-labelledby="headingOne"
            data-bs-parent="#accordionExample"
          >
            <div class="accordion-body ">
              الدفاع والمرافعة في القضايا الجنائية أمام جميع المحاكم الجنائية
              على اختلاف أنواعها ودرجاتها وحضور التحقيقات امام النيابة العامة.
            </div>
          </div>
        </div>

        <div class="accordion-item text-white  bg-dark">
          <h2 class="accordion-header " id="headingTwo">
            <button
              class="accordion-button collapsed bg-dark bg-dark"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseTwo"
              aria-expanded="false"
              aria-controls="collapseTwo"
            >
              القضايا العقارية
            </button>
          </h2>
          <div
            id="collapseTwo"
            class="accordion-collapse collapse bg-wihte"
            aria-labelledby="headingTwo"
            data-bs-parent="#accordionExample"
          >
            <div class="accordion-body">
              يقدم المكتب خدمة قانونية رائدة في المنازعات العقارية، وكذلك قضايا
              تعويضات نزع ملكية العقارات.
            </div>
          </div>
        </div>
        <div class="accordion-item text-white bg-dark text-white">
          <h2 class="accordion-header bg-dark " id="headingThree">
            <button
              class="accordion-button bg-dark collapsed bg-dark bg-dark"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseThreeee"
              aria-expanded="false"
              aria-controls="collapseThreeee"
            >
              القضايا المدنية
            </button>
          </h2>
          <div
            id="collapseThreeee"
            class="accordion-collapse collapse"
            aria-labelledby="headingThree"
            data-bs-parent="#accordionExample"
          >
            <div class="accordion-body">
              يشمل ذلك كافة أنواع وأفرع القضايا المدنية المتنوعة الناشئة عن
              المعاملات و العقود المدنية والمرافعة والدفاع عنها أمام جميع
              المحاكم على اختلاف درجاتها وأنواعها.
            </div>
          </div>
        </div>

        <div class="accordion-item text-white">
          <h2 class="accordion-header bg-dark text-white" id="headingThree">
            <button
              class="accordion-button collapsed bg-dark"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseThreee"
              aria-expanded="false"
              aria-controls="collapseThreee"
            >
              القضايا التجارية
            </button>
          </h2>
          <div
            id="collapseThreee"
            class="accordion-collapse collapse bg-dark"
            aria-labelledby="headingThree"
            data-bs-parent="#accordionExample"
          >
            <div class="accordion-body">
              وهي المنازعات التي تنشأ بين رب العمل والعامل وغالبا ما تكون هذه
              المنازعات تختص بالمرتبات أو الترقيات أو الجزاءات التأديبية، وغيرها
              من القرارات والعقود الإدارية والعمالية.
            </div>
          </div>
        </div>
        <div class="accordion-item text-white bg-dark">
          <h2 class="accordion-header bg-dark" id="headingThree">
            <button
              class="accordion-button collapsed bg-dark"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseThreeef"
              aria-expanded="false"
              aria-controls="collapseThreeef"
            >
              القضايا العمالية
            </button>
          </h2>
          <div
            id="collapseThreeef"
            class="accordion-collapse collapse "
            aria-labelledby="headingThree"
            data-bs-parent="#accordionExample"
          >
            <div class="accordion-body">
              وهي المنازعات التي تنشأ بين رب العمل والعامل وغالبا ما تكون هذه
              المنازعات تختص بالمرتبات أو الترقيات أو الجزاءات التأديبية، وغيرها
              من القرارات والعقود الإدارية والعمالية.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Textmorafat;
